import React from 'react'

import './ProductsList.css'
import ProductListItem from './ProductListItem';

import products from './products'

const ProductsList = () => {
    return (
        <div>
            <h1>Products List</h1>
            <div className="row">
                {
                    products.map(({
                        id,
                        name,
                        description,
                        type,
                        capacity,
                        price,
                        image
                    }) => (
                        <div className="col-lg-6" key={id}>
                            <ProductListItem
                                id={id}
                                name={name}
                                description={description}
                                type={type}
                                capacity={capacity}
                                price={price}
                                image={image}
                                
                            />
                        </div>
                    ))
                }
            </div>
        </div>
        
    )
}

export default ProductsList